export function VaultCdoEpochDepositQueue(
  options?: Partial<VaultContractCdoEpochQueueData>
): VaultContractCdoEpochQueueData {
  return {
    amount: options?.amount || '0',
    lastAmount: options?.lastAmount,
  }
}

export function VaultContractCdoEpochMock(
  options?: DeepPartial<VaultContractCdoEpochData>
): VaultContractCdoEpochData {
  return {
    apr: options?.apr || 0,
    lastApr: options?.lastApr || 0,
    lastInterest: options?.lastInterest || '0',
    expectedInterest: options?.expectedInterest || '0',
    deposits: options?.deposits || '0',
    duration: options?.duration || 86400,
    bufferDuration: options?.bufferDuration || 0,
    unclaimedFees: options?.unclaimedFees || '0',
    contractValue: options?.contractValue,

    startDate: options?.startDate,
    endDate: options?.endDate,
    count: options?.count,

    status: options?.status || 'WAITING',

    depositQueue: VaultCdoEpochDepositQueue(options?.depositQueue),

    instantWithdraws: VaultCdoEpochInstantWithdrawsMock(
      options?.instantWithdraws
    ),
    withdraws: VaultCdoEpochWithdrawsMock(options?.withdraws),
  }
}
